export default class ColorScheme {
  constructor(config) {
    this.colors = void 0;
    this.description = void 0;
    this.id = void 0;
    this.label = void 0;
    const {
      colors,
      description = '',
      id,
      label
    } = config;
    this.id = id;
    this.label = label != null ? label : id;
    this.colors = colors;
    this.description = description;
  }

}